import React, { Component } from 'react';
import PropTypes from 'prop-types';
import Router from 'next/router';

// graphql
import gql from 'graphql-tag';
import { Mutation, Query } from 'react-apollo';

// libs
import formatMoney from '../lib/formatMoney';

// custom components
import ErrorMessage from './ErrorMessage';

// custom styles
import Form from './styles/Form';

// GraphQL queries
const SINGLE_ITEM_QUERY = gql`
  query SINGLE_ITEM_QUERY($id: ID!) {
    item(where: { id: $id }) {
      id
      title
      description
      price
    }
  }
`;

const UPDATE_ITEM_MUTATION = gql`
  mutation UPDATE_ITEM_MUTATION($title: String!, $description: String!, $price: Int) {
    updateItem(title: $title, description: $description, price: $price) {
      id
      title
      description
      price
    }
  }
`;

class UpdateItem extends Component {
  static propTypes = {
    id: PropTypes.string,
  };

  state = {};

  handleChange = event => {
    const { name, type, value } = event.target;
    const val = type === 'number' ? parseFloat(value) : value;

    this.setState({
      [name]: val,
    });
  };

  // handleSubmit = async (event, createItem) => {
  //   // stop the from from submitting
  //   event.preventDefault();
  //   // call the mutation
  //   const res = await createItem();
  //   // change them to the single item page
  //   console.log(res);
  //   Router.push({
  //     pathname: '/item',
  //     query: { id: res.data.createItem.id },
  //   });
  // };

  render() {
    const { title, description, image, largeImage, price } = this.state;
    const { id } = this.props;
    return (
      <Query query={SINGLE_ITEM_QUERY} variables={{ id }}>
        {({ data, loading, error }) => {
          if (loading) return <div>Loading...</div>;
          if (error) return <div>Error {error.message}</div>;
          return (
            <Mutation mutation={UPDATE_ITEM_MUTATION} variables={{ title, description, image, largeImage, price }}>
              {(updateItem, { loading, error }) => (
                <Form
                  onSubmit={async e => {
                    // stop the form from submitting
                    e.preventDefault();
                    // call the mutation
                    const res = await updateItem();
                    console.log(res);
                  }}
                >
                  <ErrorMessage error={error} />
                  <fieldset disabled={loading} aria-busy={loading}>
                    <label htmlFor="title">
                      Title
                      <input
                        type="text"
                        id="title"
                        name="title"
                        placeholder="Title"
                        required
                        value={data.item.title}
                        onChange={this.handleChange}
                      />
                    </label>
                    <label htmlFor="price">
                      Price
                      <input
                        type="number"
                        id="price"
                        name="price"
                        placeholder="Price"
                        required
                        defaultValue={data.item.price}
                        onChange={this.handleChange}
                      />
                    </label>
                    <label htmlFor="description">
                      Description
                      <textarea
                        type="text"
                        id="description"
                        name="description"
                        placeholder="Enter A Description"
                        required
                        defaultValue={data.item.description}
                        onChange={this.handleChange}
                      />
                    </label>
                  </fieldset>
                  <button type="submit">Update</button>
                </Form>
              )}
            </Mutation>
          );
        }}
      </Query>
    );
  }
}

export default UpdateItem;
export { SINGLE_ITEM_QUERY, UPDATE_ITEM_MUTATION };
